import { Component, HostBinding, OnInit, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { TkProjectsService } from '../../services/tk-projects.service';
import { Project } from '../../models/tk-project.model';

@Component({
  selector: 'tk-main',
  templateUrl: './tk-main.component.html'
  // styleUrls: ['./tk-main.component.css']
})

export class TkMainComponent implements OnInit, OnDestroy {
  @HostBinding('class') classes = 'main';
  private currentProject$: Observable<Project>;

  constructor(private tkProjectsService: TkProjectsService) {}

  public ngOnInit(): void {
    this.getProject();
  }
  public ngOnDestroy(): void {
  }

  private getProject() {
    // console.log('<Main> Getting current project...');
    this.currentProject$ = this.tkProjectsService.getCurrentProject();
    // this.currentProject$.subscribe(prj => console.log('<main> Returning currentProject$: ', prj.title));
  }
}
