import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
// import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { Project } from '../models/tk-project.model';

@Injectable({
  providedIn: 'root',
})

export class TkProjectsService {
  defaultProject: string;
  // public currentProject = new Observable<Project>();
  // private project = new BehaviorSubject<Project>(null);
  currentProject: Project; // = this.project.asObservable();
  currentProject$: Observable<Project>;
  currentProjectIndex = 0;

  constructor(private httpClient: HttpClient) {
    // private extUrl = 'http://rest.ensemblgenomes.org/info/ping?content-type=application/json';
    this.defaultProject = 'assets/defaults/tk-defaults-project.json';
  }

  // private setCurrentProject(projectIndex) {
  //   this.currentProjectIndex = projectIndex;
  //   this.currentProject = this.projects$[this.currentProjectIndex];
  //   console.log('Current project: ', this.currentProject.title);
  // }

  getProject(): Observable<Project> {
    console.log('Loading default projects...');
    return this.httpClient.get<Project>(this.defaultProject);
  }

  loadDefaultProject(): Observable<Project> {
    console.log('Loading default projects...');
    this.currentProject$ = this.httpClient.get<Project>(this.defaultProject);
    return this.currentProject$;
  }

  getCurrentProject(): Observable<Project>  {
    this.loadDefaultProject();
    // this.currentProject$.subscribe(prj => console.log('Returning currentProject$: ', prj.title));
    return this.currentProject$;
  }

}
