import { Component, HostBinding, OnInit, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { TkProjectsService } from '../../services/tk-projects.service';
import { Project } from '../../models/tk-project.model';

@Component({
  selector: 'tk-sidebar',
  templateUrl: './tk-sidebar.component.html',
  styleUrls: ['./tk-sidebar.component.css']
})

export class TkSidebarComponent implements OnInit, OnDestroy {
  @HostBinding('class') classes = 'sidebar';

  private currentProject$: Observable<Project>;

  private sidemenu: any[] = [
    { title: 'Projects', link: '#' },
    { title: 'Resources', link: '#' },
    { title: 'Archives', link: '#' }
  ];

  constructor(private tkProjectsService: TkProjectsService) {}

  public ngOnInit(): void {
    this.getProject();
  }
  public ngOnDestroy(): void {
  }

  private getProject() {
    console.log('<Sidebar> Getting current project...');
    this.currentProject$ = this.tkProjectsService.getCurrentProject();
    this.currentProject$.subscribe(prj => console.log('<sidebar> Returning currentProject$: ', prj.title));
  }
}
