import { Component, HostBinding, OnInit, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { TkProjectsService } from './services/tk-projects.service';
import { Project } from './models/tk-project.model';

@Component({
  selector: 'app-tadkit',
  templateUrl: './app-tadkit.component.html',
  styleUrls: ['./app-tadkit.component.css']
})

export class AppTadkitComponent implements OnInit, OnDestroy {
  private currentProject$: Observable<Project>;

  constructor(private tkProjectsService: TkProjectsService) {}

  public ngOnInit(): void {
    this.tkDefaults();
  }
  public ngOnDestroy(): void {
  }

  private tkDefaults() {
    this.currentProject$ = this.tkProjectsService.getProject();
    // this.currentProject$.subscribe(prj => console.log('<app> Returning currentProject$: ', prj.title));
  }
}
